html.css-scroll-fixed:not(.open-only-mob) {
  width: 100% !important;
  height: 100% !important;
  max-width: 100% !important;
  max-height: 100% !important;
  margin: 0 !important;
  padding: 0 !important;
  overflow: hidden !important;
}

@media (max-width: 991px) {
  html.css-scroll-fixed.open-only-mob {
    width: 100% !important;
    height: 100% !important;
    max-width: 100% !important;
    max-height: 100% !important;
    margin: 0 !important;
    padding: 0 !important;
    overflow: hidden !important;
  }
}

.popup-default {
  position: fixed;
  width: 100%;
  width: 50%;
  height: 100%;
  left: 50%;
  top: 0;
  z-index: 9999;
  overflow-x: hidden;
  overflow-y: auto;
  padding: 20px 0;
  opacity: 0;
  visibility: hidden;
  will-change: opacity, visibility;
  transition-property: opacity, visibility;
  transition-duration: 0.2s;
}

.popup-default.active, .popup-default.is-open {
  opacity: 1;
  visibility: visible;
}

@media only screen and (max-width: 479px) {
  .popup-default {
    padding: 0;
    background-color: white;
  }
}

.popup-default.active,
.popup-default.is-open {
  display: block;
}

.popup-default__holder {
  display: table;
  position: relative;
  height: 100%;
  width: 100%;
}

.popup-default__cell {
  display: table-cell;
  vertical-align: middle;
}

.popup-default__layout {
  position: relative;
  max-width: 320px;
  margin: 0 auto;
  padding: 80px 20px 40px;
  background-color: white;
  transform: scale(0.85);
  transform: scale3d(0.85, 0.85, 1);
  will-change: transform;
  transition-property: transform;
  transition-duration: 0.2s;
}

.popup-default.active .popup-default__layout, .popup-default.is-open .popup-default__layout {
  transform: scale(1);
  transform: scale3d(1, 1, 1);
}

.popup-default__close {
  position: absolute;
  right: 0;
  top: 0;
  width: 60px;
  height: 60px;
  z-index: 2;
  cursor: pointer;
  text-indent: -9999px;
  overflow: hidden;
  background-color: #c71217;
}

.popup-default__close:before,
.popup-default__close:after {
  content: "";
  position: absolute;
  left: 18px;
  top: 28px;
  height: 3px;
  width: 24px;
  border-radius: 3px;
  background-color: white;
}

.popup-default__close:before {
  transform: rotate(45deg);
}

.popup-default__close:after {
  transform: rotate(-45deg);
}

.popup-default__close:hover {
  background-color: #f29700;
}

@media only screen and (max-width: 479px) {
  .popup-default__close {
    position: fixed;
  }
}

.popup-overlay {
  position: fixed;
  left: 0;
  left: 40%;
  top: 0;
  width: 100%;
  width: 60%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.35);
  z-index: 9998;
  opacity: 0;
  visibility: hidden;
  will-change: opacity, visibility;
  transition-property: opacity, visibility;
  transition-duration: 0.2s;
}

.popup-overlay.active, .popup-overlay.is-open {
  opacity: 1;
  visibility: visible;
}

